import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Events from './views/Eventos.vue'
import chart1 from './views/NewChart.vue'
import graficas from './views/Graficas.vue'
import helpDesk from './views/HelpDesk.vue'
import dashboardCharts from './components/charts/Dashboard.vue'
import chartFinaly from './components/charts/ShowCharts.vue'
import c_anual from './components/comensales'
import costoPromedio from './components/CostoPromedio/costoPromedio'
import costoPromedio2 from './components/CostoPromedio/index'

Vue.use(Router)

export default new Router({
  mode: 'history',

  routes: [
    // loads Home component
    {
      path: '/reportes/',
      name: 'home',
      component: Home
    },
    {
      path: '/reportes/eventos/:evento',
      name: 'evento',
      component: Events
    },
    {
      path: '/reportes/chart1',
      name: 'chart1',
      component: chart1
    },
    {
      path: '/reportes/graficas',
      name: 'graficas',
      component: graficas
    },
    {
      path: '/reportes/chartsDashboard',
      name: 'dashboardCharts',
      component: dashboardCharts
    },
    {
      path: '/reportes/chartFinaly',
      name: 'chartFinaly',
      component: chartFinaly
    },
    {
      path: '/reportes/eventos/',
      name: 'charts Events',
      component: Events
    },
    {
      path: '/reportes/comensales/anual',
      name: 'Comensales',
      component: c_anual
    },
    {
      path: '/reportes/costoPromedio',
      name: 'Costo Promedio',
      component: costoPromedio
    },
    {
      path: '/help-desk',
      name: 'help-desk',
      component: helpDesk
    },
    {
      path: '/reportes/costoPromedio2',
      name: 'Costo Promedio2 ',
      component: costoPromedio2
    },
    {
      path: '/page/:sectionSlug',
      name: 'dynamicContent',
      // route level code-splitting
      // this generates a separate chunk (dynamicContent.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "dynamicContent" */ './views/DynamicContent.vue')
    }
  ]
})
