<script>
// CommitChart.js
import { Line } from 'vue-chartjs'
import axios from 'axios'
export default {
  extends: Line,
  data () {
    return {
      payload: {
        opcion: 'contratos',
      }
    }
  },
  mounted () {
      this.loadData()
  },
  methods: {
    loadData () {
        let personForm = this.toFormData(this.payload)
        // eslint-disable-next-line no-console
        console.log("payload", this.febrero)
        axios
        .post('https://villaconin.mx/reportes/mysqli.php', personForm)
        .then(response => {
          // eslint-disable-next-line no-console
          console.log(response)
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
          // Overwriting base render method with actual data.
          this.renderChart({
              labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
              datasets: [
                  {
                      label: 'GitHub Commits',
                      backgroundColor: '#f87979',
                      data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11]
                  },
                  {
                      label: 'GitHub Commits',
                      backgroundColor: '#f00',
                      data: [20, 23, 42, 19, 60, 50, 19, 20, 40, 25, 16, 1]
                  }
             ]
          })
    },
     toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      }
} 
}
</script>
