<script>
import { Bar } from 'vue-chartjs'
const axios = require('axios');
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    evento: {
      type: String
    }
  },
  data () {
      return {
        dat: [],
        label: []
      }
    },
    watch: {
        evento(val) {
            this.label = []
            this.dat = []
            let payload = {
                opcion: 'eventosDiaEjecucion',
                tipo: val
            }   
            let personForm = this.toFormData(payload);
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', personForm)
            .then(response => {
                _.forEach(response.data, (val, key)  => {
                    this.dat.push(val)
                    this.label.push(key)
                })
                this.renderChart({
                    // labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
                    labels: this.label,
                    datasets: [
                        {
                            label: 'Dia de Ejecucin: ' + val,
                            backgroundColor: ['#154360', '#616A6B', '#6E2C00', '#71FF33', '#F4D03F', '#3380FF', '#2ECC71', '##3498DB', '#A569BD', '#EC7063', '#154360'],
                            data: this.dat
                        }
                    ]
                })
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
        }
    },
  mounted () {
    // Overwriting base render method with actual data.
  },
  methods: {
      toFormData: function(obj) {
          let formData = new FormData();
          for(let key in obj) {
              formData.append(key, obj[key]);
            }
            return formData;
        }
  }
}
</script>
