<template>
    <form-wizard 
        title="HelpDesk"
        subtitle="Siga los pasos para generar un issue"
        color="#121211"
        @on-complete="createdIssue" 
    >
        <tab-content title="Identificacin de issue">
            <label for="">Categoria del issue:</label>
            <b-form-select v-model="selected" :options="options" class="mb-3">
            </b-form-select>
            <label for="">Titulo del issue</label>
            <b-form-input v-model="issueTitle" placeholder="Titulo del issue"></b-form-input>
            <br>
            <label for="">Descripcin</label>
            <b-form-textarea
            id="textarea"
            v-model="descriptionIssue"
            placeholder="Ingrese una descripcin que describa la problematica."
            rows="3"
            max-rows="6"
            ></b-form-textarea>
        </tab-content>
        <tab-content title="Detalles del issue">
            <label for="">Prioridad del Issue:</label>
            <b-form-select v-model="priority" :options="priorityOptions" class="mb-3">
            </b-form-select>
            <div v-if="selected === 'soporte'">
                <label for="">Detalles especificos del equipo:</label>
                <b-form-input v-model="issue.detalle" placeholder=""></b-form-input>
            </div>
            <div v-if="selected === 'red'">
                <label for="">Detalles especificos de el problema de red (salon(es), ):</label>
                <b-form-input v-model="issue.detalle" placeholder=""></b-form-input>
            </div>
            <div v-if="selected === 'sistema'">
                <label for="">Pagina del problema (url completa):</label>
                <b-form-input v-model="issue.detalle" placeholder=""></b-form-input>
                <label for="">:</label>
                <b-form-input v-model="issue.detalle" placeholder=""></b-form-input>
                <label for="">Cual seria el resultado correcto:</label>
                <b-form-input v-model="issue.detalle2" placeholder=""></b-form-input>
            </div>
            <div v-if="selected === 'otros'">
                <label for="">Por favor describa con detalle su problema</label>
                <b-form-textarea
                id="textarea"
                v-model="issue.detalle"
                placeholder="Ingrese una descripcin detallada de el problema que presenta y cual seria la solucin si existiera."
                rows="3"
                max-rows="6"
                ></b-form-textarea>
            </div>
            <b-form-file
            v-model="file"
            :state="Boolean(file)"
            placeholder="Seleccione un archivo"
            drop-placeholder="Drop file here..."
            accept="image/jpeg, image/png, image/gif"
            ></b-form-file>
            <div class="mt-3">Archivo seleccionado: {{ file ? file.name : '' }}</div>
        </tab-content>
        <tab-content title="Confirmacin">
            <div>
                <b-card no-body class="overflow-hidden" style="max-width: 540px;">
                    <b-row no-gutters>
                    <b-col md="6">
                        <b-card-img src="https://picsum.photos/400/400/?image=20" class="rounded-0"></b-card-img>
                    </b-col>
                    <b-col md="6">
                        <b-card-body :title="issueTitle">
                            <b-card-text>
                                {{descriptionIssue}}
                            </b-card-text>
                            <b-card-text>
                                {{issue.detalle}}
                            </b-card-text>
                            <b-card-text>
                                {{issue.detalle2}}
                            </b-card-text>
                        </b-card-body>
                    </b-col>
                    </b-row>
                </b-card>
            </div>
        </tab-content>
    </form-wizard>
</template>
<script lang="ts">
import Vue from 'vue'
import { firebase, db } from '../firebase'
export default Vue.extend({
    data () {
        return {
            file: null,
            selected: null,
            options: [
                { value: null, text: 'Seleccione una categoria del problema' },
                { value: 'soporte', text: 'Probelmas en equipos o impresoras' },
                { value: 'red', text: 'Problemas de red' },
                { value: 'sistema', text: 'Problemas con el sistema' },
                { value: 'otros', text: 'Otros' },
            ],
            issueTitle: '',
            descriptionIssue: '',
            issue: {
                detalle: '',
                detalles2: ''
            },
            priority: null,
            priorityOptions: [
                { value: null, text: 'Seleccione una prioridad del problema' },
                { value: 'bajo', text: 'Prioridad baja' },
                { value: 'medio', text: 'Prioridad media' },
                { value: 'alto', text: 'Prioridad alta' }
            ]
        }
    },
    watch: {
        file () {
            this.subirImagen()
        }
    },
    methods: {
        subirImagen () {
            // Create a root reference
            const storageRef = firebase.storage().ref();
            // Create a reference to 'mountains.jpg'
            // const mountainsRef = storageRef.child('mountains.jpg');
            // Create a reference to 'images/mountains.jpg'
            const mountainImagesRef = storageRef.child('images/mountains.jpg');
            mountainImagesRef.put(this.file).then((snapshot) => {
                // eslint-disable-next-line no-console
                console.log('Uploaded a blob or file!', snapshot);
            });
        },
        createdIssue () {
            db.ref('/issues').push({
                title: this.issueTitle,
                description: this.descriptionIssue,
                category: this.selected,
                priority: this.priority,
                details: this.issue
            })
            this.$bvToast.toast(`Issue creado correctamente, nuestro equipo ha recibido el issue, en cuanto se tenga respuesta del mismo el equipo de Qrrodigo se comunicara con ustedes.`, {
            title: 'Creacion de nuevo issue',
            autoHideDelay: 5000,
            appendToast: true,
            variant: 'success'
            })
        }
    } 
})
</script>