<template>
    <div>
        <b-card-group deck class="mt-5">
            <b-card
            header-tag="header"
            footer="Grafica lineal."
            footer-tag="footer"
            title="Graficas Anuales"
            >
                <b-card-text>Graficas anuales por comensales y dinero.</b-card-text>
                <b-button variant="primary" @click="showCharts('anual')">Entrar</b-button>
            </b-card>

            <b-card
            header-tag="header"
            footer="Grafica barras."
            footer-tag="footer"
            title="Graficas Mensuales"
            >
                <b-card-text>Graficas mensulaes por evento, salon, comensales y dinero.</b-card-text>
                <b-button variant="primary" @click="showCharts('mensual')">Entrar</b-button>
            </b-card>
             <b-card
            header-tag="header"
            footer="Grafica barras."
            footer-tag="footer"
            title="Graficas Anual"
            >
                <b-card-text>Graficas anuales totales.</b-card-text>
                <b-button variant="primary" @click="showCharts('Grafica3')">Entrar</b-button>
            </b-card>
        </b-card-group>
    </div>
</template>
<script>
export default {
    methods: {
        showCharts (val) {
            // eslint-disable-next-line no-console
            // console.log('entro aca')
            if (val === 'anual') {
                this.$router.push({ name: 'dashboardCharts', params: { grafica: 'Anual' }})
            }
            if (val === 'mensual') {
                this.$router.push({ name: 'dashboardCharts', params: { grafica: 'Mensual' }})
            }
            if (val === 'Grafica3') {
                this.$router.push({ name: 'dashboardCharts', params: { grafica: 'Grafica3' }})
            }
        }
    } 
}
</script>