<template>
  <div>
    <br>
    <div class="col-lg-12" align="center">
      <p>Costo Promedio</p>
      <div class="col-lg-4 verticalAlign">
        <datepicker v-model="fechaInicio" placeholder="Seleccione el Inicio"  ></datepicker>
      </div>
      <div class="col-lg-4 verticalAlign">
        <datepicker v-model="fechaFin" placeholder="Seleccione el Final"  ></datepicker>
      </div>
    </div>
    <br/>
    <br/>
    <div align ="center" class="col-lg-12" v-if="fechaInicio !== ''">
      <p>Desde: {{ fechaInicio }}  hasta: {{ fechaFin }} </p>
    </div>
    <div align ='left' class="col-lg-6 verticalAlign">
      <!-- Single button triggers two "<b-collapse>" components -->
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cantidad de Eventos <span class="producto"> {{ nEventos }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cantidad de Comensales <span class="producto"> {{ nComensales }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Total Cobrado <span class="producto">$ {{ formatPrice(totalCobrado) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Eventos Adicionales <span class="producto"> {{ eventosAdicionales }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Eventos de Recaudacion <span class="producto">$ {{ formatPrice(eventosRecaudacion) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Venta de vinos <span class="producto">$ {{ formatPrice(totalVentaVinos) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Ventas <span class="producto">$ {{ formatPrice(totalVentas) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Cancelaciones <span class="producto">$ {{ formatPrice(totalCancelaciones) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Total Cobrado <span class="producto">$ {{ formatPrice(totalVentas + totalCobrado + totalVentaVinos + eventosRecaudacion + totalCancelaciones) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gasto Insumo<span class="producto">$ {{ formatPrice(totalInsumos) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gasto Activo <span class="producto">$ {{ formatPrice(totalGastoActivo) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gasto Operativo <span class="producto">$ {{ formatPrice(totalGastoOperativo) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Nomina <span class="producto">$ {{ formatPrice(totalNomina) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Premio de lealtad <span class="producto">$ {{ formatPrice(totalPremioLealtad) }}</span></b-button>
      </div>

      <!-- Elements to collapse -->
      <b-collapse id="collapse-a" class="mt-2">
        <b-card>I am collapsible content A!</b-card>
      </b-collapse>
      <b-collapse id="collapse-b" class="mt-2">
        <b-card>I am collapsible content B!</b-card>
      </b-collapse>
    </div>
    <!-- <pre>
    {{ totalInsumos }}
    {{ totalGastoActivo }}
    {{ totalNomina}}
    {{ totalPremioLealtad }}
     = {{ totalInsumos + totalGastoActivo + totalNomina + totalPremioLealtad }}
    </pre> -->
    <div align ='left' class="col-lg-6 verticalAlign">
      <div>      
        <b-button v-b-toggle.collapse-a.collapse-b>Total Gastado <span class="producto">$ {{ formatPrice(totalInsumos + totalGastoActivo + totalNomina + totalPremioLealtad) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Costo Promedio por Comensal <span class="producto">$ {{ formatPrice((totalInsumos + totalGastoActivo + totalNomina + totalPremioLealtad)/nComensales) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Precio Promedio por Comensal <span class="producto">$ {{ formatPrice(totalCobrado/nComensales) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Utilidad Bruta <span class="producto">$ {{ formatPrice(totalCobrado - (totalInsumos + totalGastoActivo + totalNomina + totalPremioLealtad)) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Gastos Personales <span class="producto">$ {{ formatPrice(totalGastoPersonal) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Inversion <span class="producto">$ {{ formatPrice(totalGastoPersonal) }}</span></b-button>
      </div>
      <div>
        <b-button v-b-toggle.collapse-a.collapse-b>Inversion Vinos <span class="producto">$ {{ formatPrice(totalGastoPersonal) }}</span></b-button>
      </div>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker';
const axios = require('axios');
const moment = require("moment")
  export default {
    components: {Datepicker},
    data() {
      return {
        fechaInicio: '',
        fechaFin: '',
        nEventos: 0,
        nComensales: 0,
        totalCobrado: 0,
        eventosAdicionales: 0,
        eventosRecaudacion: 0,
        totalVentaVinos: 0,
        totalVentas: 0,
        totalCancelaciones: 0,
        totalInsumos: 0,
        totalGastoActivo: 0,
        totalGastoOperativo: 0,
        totalNomina: 0,
        totalPremioLealtad: 0,
        // GASTO INSUMO + GASTO ACTIVO + GASTO OPERATIVO + NOMINA + PREMIO DE LEALTAD = TOTAL COBRADO       
        totalGastado: 0,
        totalGastoPersonal: 0
      }
    },
    computed: {
      sortOptions() {
        // Create an options list from our fields
        return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
      }
    },
    watch:{
      fechaInicio (val) {
        this.fechaInicio = moment(val).format('YYYY-MM-DD')
        if (this.fechaFin !== '') {
          this.numeroComensales()
          this.numeroEventos()
        }
      },
      fechaFin (val) {
        // eslint-disable-next-line no-console
        this.fechaFin = moment(val).format('YYYY-MM-DD')
        if (this.fechaFin !== '') {
          this.numeroEventos()
          this.numeroComensales()
          this.cobrado()
          this.numeroEventosAdicionales()
          this.numeroEventosRecaudacion()
          this.ventaVinos()
          this.ventas()
          this.gatoInsumo()
          this.gatoActivo()
          this.gastoOperativo()
          this.nomina()
          this.premioDeLealtad()
          this.gastado()
          this.gastoPersonal()
        }
      }
    },
    methods: {
      numeroEventos () {
        this.getData('numeroEventos').then((data) => {
            this.nEventos = data;
        })
      },
      numeroComensales () {
        this.getData('numeroComensales').then((data) => {
            this.nComensales = data;
        })
      },
      cobrado () {
        this.getData('totalCobrado').then((data) => {
            this.totalCobrado = data
        })
      },
      numeroEventosAdicionales () {
        this.getData('eventosAdicionales').then((data) => {
            this.eventosAdicionales = data;
        })
      },
      numeroEventosRecaudacion () {
        this.getData('eventosRecaudacion').then((data) => {
            this.eventosRecaudacion = data
        })
      },
      ventaVinos () {
        this.getData('ventaVinos').then((data) => {
            this.totalVentaVinos = data
        })
      },
      ventas () {
        this.getData('ventas').then((data) => {
            this.totalVentas = data
        })
      },
      cancelaciones () {
        this.getData('cancelaciones').then((data) => {
          this.totalCancelaciones = data
        })
      },
      gatoInsumo () {
        this.getData('gastoInsumo').then((data) => {
          this.totalInsumos = data
        })
      },
      gatoActivo () {
        this.getData('gastoActivo').then((data) => {
          this.totalGastoActivo = data
        })
      },
      gastoOperativo () {
        this.getData('gastoOperativo').then((data) => {
          this.totalGastoOperativo = data
        })
      },
      nomina () {
        this.getData('nomina').then((data) => {
          this.totalNomina = data
        })
      },
      premioDeLealtad () {
        this.getData('premioDeLealtad').then((data) => {
          this.totalPremioLealtad = data
        })
      },
      gastado () {
        this.totalGastado = this.totalInsumos + this.totalGastoActivo + this.totalGastoOperativo + this.totalNomina + this.totalPremioLealtad
          // eslint-disable-next-line no-console
          console.log('totalGastado', this.totalGastado)
      },
      gastoPersonal () {
        this.getData('gastoPersonal').then((data) => {
          this.totalGastoPersonal = data
        })
      },
      getData: function (opcion) {
        return new Promise((resolve, reject) => {
          try {
            let payload = {
              fecha1: this.fechaInicio,
              fecha2: this.fechaFin,
              opcion: opcion
            }
            let personForm = this.toFormData(payload);
            axios
            .post('https://villaconin.mx/reportes/costoPromedio.php', personForm)
            .then(response => {
              resolve(response.data)
            })
            .catch(error => {
              this.errored = true
              // eslint-disable-next-line no-console
              console.log(error)
            })
            .finally(() => this.loading = false)
          }
          catch (error) {
            reject(error)
          }
        })
      },
      toFormData(obj) {
        let formData = new FormData();
        for(let key in obj) {
          formData.append(key, obj[key]);
        }
        return formData;
      },
      formatPrice(value) {
        let val = (value/1).toFixed(2)
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
      }
    }
  }
</script>
<style>
  .producto {
    color: darkorange;
  }
</style>
