<script>
// CommitChart.js
import { Bar } from 'vue-chartjs'
import axios from 'axios'
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    years: {
      type: Array
    },
    event: {
      type: String
    },
    type: {
      type: String
    },
    salon: {
      type: String
    }
  },
  data () {
    return {
      payload: {
        opcion: '',
        evento: '',
        salon: ''
      },
      sets: [],
      backgrounds: {
        2017: 'rgba(244, 0, 0, 0.1)',
        2018: 'rgba(0, 48, 244, 0.1)',
        2019: 'rgba(255, 206, 86, 0.2)',
        2020: 'rgba(86, 2455, 240, 0.1)',
        2021: 'rgba(153, 102, 255, 0.2)',
        2022: 'rgba(255, 159, 64, 0.2)',
        2023: '#DE00C3',
        2024: '#DE8A00',
      }
    }
  },
  watch: {
    years () {
      this.loadData()
    },
    event (val) {
      this.payload.salon = ""
      this.payload.evento = val
      this.loadData()
    },
    salon (val) {
      this.payload.salon = val
      this.payload.evento = ""
      this.loadData()
    },
    type (val) {
      if (val === "evento") {
        this.payload.opcion = "generalChart2"
        this.payload.salon = ""
        this.payload.evento = this.event
      }
      if (val === "salon") {
        this.payload.opcion = "generalChart3"
        this.payload.salon = this.salon
        this.payload.evento = ""
      }
      this.loadData()
    }
  },
  mounted () {
    if (this.type === "evento") {
        this.payload.opcion = "generalChart2"
        this.payload.salon = ""
        this.payload.evento = this.event
    }
    if (this.type === "salon") {
      this.payload.opcion = "generalChart3"
      this.payload.salon = this.salon
      this.payload.evento = ""
    }
  },
  methods: {
    loadData () {
      this.sets = []
      if (this.years === '') {
        this.payload.years = [2019]
      } else {
        this.payload.years = this.years
      }
        let personForm = this.toFormData(this.payload)
        // eslint-disable-next-line no-console
        console.log(this.payload)
        axios
        .post('https://villaconin.mx/reportes/mysqli.php', personForm)
        .then(response => {
          // eslint-disable-next-line no-console
          console.log("RESPONS", response)
          if (response.data !== "") {
            setTimeout(() => {
              let d = []
              let keys = []
              _.forEach(response.data, (val, key) => {
                keys.push(key)
                d.push(val.total)
              })
              this.renderChart({
                  labels: keys,
                  datasets: [
                    {
                      label: "Comensales",
                      backgroundColor: ['#f87979', '#FF5733', '#FFBB33', '#71FF33', '#33FFE3', '#3380FF', '#4F33FF', '#9633FF', '#FF33F0', '#FF33AF', '#FF3364'],
                      data: d
                    }
                  ]
                  }, {responsive: true, maintainAspectRatio: false})
            }, 1000);
          }

        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
    },
     toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      }
    } 
}
</script>
