<template>
  <div class="main-content">
    <div class="main-content__top">
        <h1 class="main-content__title">
            Eventos {{selected}}
        </h1>
    </div>
    <div class="main-content__body">
      <b-form-select  v-model="selected" :options="options" />
      <BarComensales class="col-lg-6" :height="350" :evento ="valueChart"/>
      <BarDinero class="col-lg-6" :height="350" :evento ="valueChart"/>
      <BarDineroEjecucion class="col-lg-6" :height="350" :evento ="valueChart"/>
      <Bar />
    </div>
  </div>
</template>
<script>
const axios = require('axios');
import BarComensales from '../components/charts/Bar'
import BarDinero from '../components/charts/BarDinero'
import BarDineroEjecucion from '../components/charts/BarDineroDiaEjecucion'
export default {
    name: 'eventos',
    components: { BarComensales, BarDinero, BarDineroEjecucion },
    data () {
        return {
            payload: {
                opcion: 'eventos',
                tipo: ''
            },
            selected: null,
            options: [
                { value: null, text: 'Por favor seleccione un tipo de evento' }
            ],
            valueChart: ''
        }
    },
    mounted () {
    let personForm = this.toFormData({opcion: 'tipo_evento'});
    // eslint-disable-next-line no-console
      axios
      .post('https://villaconin.mx/reportes/mysqli.php', personForm)
      .then(response => {
          response.data.forEach(element => {
              this.options.push({ value: element, text: element})
          });
      })
      .catch(error => {
        // eslint-disable-next-line no-console
        console.log(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
  },
  watch: {
    selected (val) {
      this.valueChart = val
    }
  },
  methods: {
    changeSelected () {
      // eslint-disable-next-line no-console
      console.log("SELECTED", this.selected)
      this.payload.tipo = this.selected
      this.payload.opcion = "eventos"
      this.valueChart = this.payload
    },
    getDataChart: function () {
        let personForm = this.toFormData(this.payload);
    // eslint-disable-next-line no-console
    console.log("personForm", personForm)
      axios
      .post('https://villaconin.mx/reportes/mysqli.php', personForm)
      .then(response => {
          // eslint-disable-next-line no-console
          console.log(response)
          this.valueChart = response.data
      })
      .catch(error => {
          // eslint-disable-next-line no-console
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
    },
    toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      }
  },
  created () {
  }
}
</script>

