<template>
    <b-container class="bv-example-row">
        <b-row>
            <b-col>
                 <b-form-group>
                <template slot="label">
                    <b>Seleccione los aos:</b><br />
                    <b-form-checkbox
                    v-model="allSelectedYearsNew"
                    :indeterminate="indeterminate"
                    aria-describedby="Years"
                    aria-controls="years"
                    @change="toggleAllYears"
                    >
                    {{ allSelectedYearsNew ? 'Quitar Todos' : 'Todos' }}
                    </b-form-checkbox>
                </template>

                <b-form-checkbox-group
                    id="years"
                    stacked
                    v-model="selectedYearsNew"
                    name="flavs"
                    :options="yearsNew"
                    class="ml-4"
                    aria-label="Individual years"
                />
                </b-form-group>
            </b-col>
        </b-row>
        <b-row v-if="type === 'ejecucion'">
          Comensales
          <comensalesAnual :years="selectedYearsNew"/>
        </b-row>
        <b-row v-if="type === 'ejecucion'">
          Dinero
          <dineroAnual :years="selectedYearsNew"/>
        </b-row>
        <b-row v-if="type === 'venta'">
          Comensales
          <comensalesAnualVenta :years="selectedYearsNew"/>
        </b-row>
        <b-row v-if="type === 'venta'">
          Dinero
          <dineroAnualVenta :years="selectedYearsNew"/>
        </b-row>
    </b-container>
</template>
<script>
const axios = require('axios');
import comensalesAnual from './anual/comensales'
import dineroAnual from './anual/dinero'
import comensalesAnualVenta from './anual/comensalesVentas'
import dineroAnualVenta from './anual/dineroVentas'
// import ChartRooms from '../components/charts/chart3'
var _ = require('lodash');
export default {
  props: {
    type: {
      type: String
    }
  },
  components: {
    comensalesAnual,
    dineroAnual,
    comensalesAnualVenta,
    dineroAnualVenta
  },
  data () {
    return {
      allSelectedYearsNew: false,
      indeterminate: false,
      selectedYearsNew: [],
      yearsNew: [],
    }
  },
  mounted () {
    let personForm = this.toFormData({opcion: 'yearSistem'});
        axios
        .post('https://villaconin.mx/reportes/mysqli.php', personForm)
        .then(response => {
            _.forEach(response.data, (elementc) => {
                 this.yearsNew.push(elementc)
            })
        })
        .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.errored = true
        })
        .finally(() => this.loading = false)
  },
  methods: {
    toggleAllYears(checked) {
      this.selectedYearsNew = checked ? this.years.slice() : []
    },
    toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
      }
      return formData;
    }
  }
}
</script>
