<script>
// CommitChart.js
import { Line } from 'vue-chartjs'
import axios from 'axios'
var _ = require('lodash');
export default {
  extends: Line,
  props: {
    years: {
      type: Array
    }
  },
  data () {
    return {
      payload: {
        opcion: 'anualDineroVentas',
      },
      sets: [],
      backgrounds: {
        2017: 'rgba(244, 0, 0, 0.1)',
        2018: 'rgba(0, 48, 244, 0.1)',
        2019: 'rgba(255, 206, 86, 0.2)',
        2020: 'rgba(86, 2455, 240, 0.1)',
        2021: 'rgba(153, 102, 255, 0.2)',
        2022: 'rgba(255, 159, 64, 0.2)',
        2023: '#DE00C3',
        2024: '#DE8A00',
      }
    }
  },
  mounted () {
      this.loadData()
  },
  watch: {
    years () {
      this.sets = []
      this.loadData()
    }
  },
  methods: {
    loadData () {
      if (this.years === '') {
        this.payload.years = [2019]
      } else {
        this.payload.years = this.years
      }
        let personForm = this.toFormData(this.payload)
        // eslint-disable-next-line no-console
        axios
        .post('https://villaconin.mx/reportes/mysqli.php', personForm)
        .then(response => {
          _.forEach(response.data, (val, key) => {
            let d = []
            let label = key
            _.forEach(val, (v) => { // meses
              d.push(v.total)
            })
            this.sets.push({
              label: label,
              backgroundColor: this.backgrounds[key],
              data: d
            })
          })
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
          // Overwriting base render method with actual data.
          setTimeout(() => {
             this.renderChart({
              labels: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
              datasets: this.sets
          }, {responsive: true, maintainAspectRatio: false})
          }, 1000);
    },
     toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      }
} 
}
</script>
