<script>
import { Bar } from 'vue-chartjs'
const axios = require('axios');
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    room: {
      type: String
    },
    year: {
      type: Array
    }
  },
  data () {
      return {
        dat: [],
        label: [],
        colorNumber: 1,
        backgroundColor: ['#922B21', '#1A5276', '#F4D03F', '#3498DB', '#9B59B6', '#DC7633', '#5D6D7E', '#33F6FF', '#3346FF', '#319900', '#FFC900', '#292929']
      }
    },
    watch: {
      year() {
            this.label = []
            this.dat = []
            let payload = {
                opcion: 'generalChart3',
                years: this.year,
                salon: this.room
            }   
            let personForm = this.toFormData(payload);
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', personForm)
            .then(response => {
                _.forEach(response.data, (val, key)  => {
                    this.dat.push(val.total)
                    this.label.push(key)
                })
                this.renderChart({
                    // labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
                    labels: this.label,
                    datasets: [
                        {
                            label: "Comensales",
                            backgroundColor: ['#f87979', '#FF5733', '#FFBB33', '#71FF33', '#33FFE3', '#3380FF', '#4F33FF', '#9633FF', '#FF33F0', '#FF33AF', '#FF3364'],
                            data: this.dat
                        }
                    ]
                })
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
      },
      yearsss () {
        this.colorNumber = 0
        this.dat = []
        const t = this
        this.getData().then((data) => {
          _.forEach(data, (val, key) => {
            this.colorNumber++
            this.dat.push({
              label: key + ' ' + val.tipo,
              backgroundColor: this.backgroundColor[this.colorNumber],
              data: val.total
            })
          })
          t.renderChart({
            labels: this.year,
            // labels: this.label,
            datasets: this.dat
          })
        })
      }
    },
  mounted () {
  },
  methods: {
    getData: function async () {
       return new Promise((resolve, reject) => {
        try {
          const payloadX = {
            opcion: 'generalChart2',
            years: this.year,
            eventos: this.event
          }   
          // eslint-disable-next-line no-console
          let personForm = this.toFormData(payloadX);
          axios
          .post('https://villaconin.mx/reportes/mysqli.php', personForm)
          .then(response => {
              resolve(response.data)
          })
          .catch(error => {
            this.errored = true
            // eslint-disable-next-line no-console
            console.log(error)
          })
          .finally(() => this.loading = false)
        }
        catch (error) {
            reject(error)
        }
      })
    },
    toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
      }
      return formData;
    }
  }
}
</script>
