<script>
import { Bar } from 'vue-chartjs'
const axios = require('axios');
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    evento: {
      type: String
    }
  },
  data () {
      return {
        dat: [],
        label: []
      }
    },
    watch: {
        evento(val) {
            this.label = []
            this.dat = []
            let payload = {
                opcion: 'eventosDiaFirma',
                tipo: val
            }   
            let personForm = this.toFormData(payload);
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', personForm)
            .then(response => {
                _.forEach(response.data, (val, key)  => {
                    this.dat.push(val)
                    this.label.push(key)
                })
                this.renderChart({
                    // labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
                    labels: this.label,
                    datasets: [
                        {
                            label: 'Firma de contrato: ' + val,
                            backgroundColor: ['#DD1100', '#F4B643', '#C6F443', '#43F45E', '#43F4C4', '#43C4F4', '#4383F4', '#4843F4', '#A643F4', '#F443C1', '#F44343'],
                            data: this.dat
                        }
                    ]
                })
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
        }
    },
  mounted () {
    // Overwriting base render method with actual data.
  },
  methods: {
      toFormData: function(obj) {
          let formData = new FormData();
          for(let key in obj) {
              formData.append(key, obj[key]);
            }
            return formData;
        }
  }
}
</script>
