<script>
import menuData from './support/menu-data';
import kebabCase from 'lodash/kebabCase';
import { db } from '@/firebase'
// import {db} from '@/firebase'

export default {
  name: 'Menu',

  data(){
    return {
      contextSection: '',
      menuItens: [],
      menuData: menuData,
      activeSubMenu: '',
      email: '',
      password: '',
      passwordState: null,
      emailState: null,
      submittedNames: []
    }
  },

  methods: {
    checkFormValidity() {
      const valid = this.$refs.form.checkValidity()
      this.emailState = valid ? 'valid' : 'invalid'
      return valid
    },
    resetModal() {
      this.email = ''
      this.emailState = null
    },
    handleOk(bvModalEvt) {
      this.$router.push('/help-desk');
      window.bus.$emit('menu/help-desk');
      // eslint-disable-next-line no-console
      // firebase.auth().createUserWithEmailAndPassword(email, password)
      //     .catch(function(error) {
      //   // Handle Errors here.
      //   var errorCode = error.code;
      //   var errorMessage = error.message;
      //   if (errorCode == 'auth/weak-password') {
      //     alert('The password is too weak.');
      //   } else {
      //     alert(errorMessage);
      //   }
      //   console.log(error);
      // })

      // Prevent modal from closing
      bvModalEvt.preventDefault()
      db.ref('/link').push({"algo": "algo"})
      // Trigger submit handler
      this.handleSubmit()
    },
    handleSubmit() {
      // Exit when the form isn't valid
      if (!this.checkFormValidity()) {
        return
      }
      // Push the name to submitted names
      this.submittedNames.push(this.name)
      // Hide the modal manually
      this.$nextTick(() => {
        this.$refs.modal.hide()
      })
    },
    openProjectLink() {
      alert('You could open the project frontend in another tab here, so the logged admin could see changes made to the project ;)');
    },

    updateMenu(context) {
      this.contextSection = context;
      this.menuItens = this.menuData[context];
      // eslint-disable-next-line no-console
      console.log(context)
      if (context === 'home') {
        this.$router.push('/reportes/');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'eventos') { 
        this.$router.push('/reportes/eventos');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'chart1') { 
        this.$router.push('/reportes/chart1');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'costoPromedio') { 
        this.$router.push('/reportes/costoPromedio');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'costoPromedio2') { 
        this.$router.push('/reportes/costoPromedio2');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'graficas') { 
        this.$router.push('/reportes/graficas/');
        window.bus.$emit('menu/closeMobileMenu');
      }
      if (context === 'villaConin') {
        window.location.href = "http://villaconin.mx" 
      }
    },

    highlightSection(section) {
      return {
        'menu__link': true,
        'menu__link--active': section === this.contextSection,
      };
    },

    subMenuClass(subMenuName) {
      return {
        'context-menu__link': true,
        'context-menu__link--active': this.activeSubMenu === subMenuName,
      };
    },

    closeContextMenu() {
      this.contextSection = '';
      this.menuItens = [];
    },

    openSection(item) {
      this.activeSubMenu = item.txt;

      this.$router.push(this.getUrl(item));
      window.bus.$emit('menu/closeMobileMenu');
    },

    getUrl(item) {
      let sectionSlug = kebabCase(item.txt);

      return `${item.link}/${sectionSlug}`;
    }

  },

  computed: {
    showContextMenu () {
      return this.menuItens.length;
    },
  }

}
</script>
