<script>
// CommitChart.js
import { Bar } from 'vue-chartjs'
import axios from 'axios'
var _ = require('lodash');
export default {
  extends: Bar,
  props: {
    years: {
      type: Array
    },
    type: {
      type: String
    },
    event: {
      type: String
    },
    room: {
      type: String
    },
    detalle: {
      type: String
    }
  },
  data () {
    return {
      payload: {
        opcion: '',
        evento: '',
        salon: ''
      },
      sets: [],
      backgrounds: {
        2017: 'rgba(244, 0, 0, 0.1)',
        2018: 'rgba(0, 48, 244, 0.1)',
        2019: 'rgba(255, 206, 86, 0.2)',
        2020: 'rgba(86, 2455, 240, 0.1)',
        2021: 'rgba(153, 102, 255, 0.2)',
        2022: 'rgba(255, 159, 64, 0.2)',
        2023: '#DE00C3',
        2024: '#DE8A00',
      }
    }
  },
  mounted () {
    if (this.detalle === 'venta') { // dia de venta 
      if (this.type  === 'evento') {
        this.payload.opcion = 'mensualDineroEvento'
        this.payload.evento = this.event
        this.payload.salon = ""
      }
      if (this.type === 'salon') {
        this.payload.opcion = 'mensualDineroSalon2'
        this.payload.salon = this.room
        this.payload.evento = ""
      }
    }
    if (this.detalle === 'ejecucion') { // dia de ejecucions
      if (this.type  === 'evento') {
        this.payload.opcion = 'mensualDineroEventoEjecucion'
        this.payload.evento = this.event
        this.payload.salon = ""
      }
      if (this.type === 'salon') {
        this.payload.opcion = 'mensualDineroSalonejEcucion'
        this.payload.salon = this.room
        this.payload.evento = ""
      }
    }
  },
  watch: {
    years () {
      // eslint-disable-next-line no-console
      console.log(this.type, this.event, this.room)
      this.sets = []
      this.loadData()
      this.payload.evento = this.event
      this.payload.salon = this.room
    },
    type (val) {
      // eslint-disable-next-line no-console
      console.log('************ TYPE ********', val)
        this.payload.evento = this.event
        this.payload.salon = this.room
        if (this.detalle === 'venta') { // dia de venta 
            if (val  === 'evento') {
              this.payload.opcion = 'mensualDineroEvento'
            }
            if (val === 'salon') {
              this.payload.opcion = 'mensualDineroSalon2'
            }
        }
        if (this.detalle === 'ejecucion') { // dia de venta 
            if (val  === 'evento') {
              this.payload.opcion = 'mensualDineroEventoEjecucion'
            }
            if (val === 'salon') {
              this.payload.opcion = 'mensualDineroSalonejEcucion'
            }
        }

    },
    event () {
        this.payload.evento = this.event
        this.payload.salon = this.room
    }
  },
  methods: {
    loadData () {
      this.sets = []
      if (this.years === '') {
        this.payload.years = [2019]
      } else {
        this.payload.years = this.years
      }
        let personForm = this.toFormData(this.payload)
        // eslint-disable-next-line no-console
        axios
        .post('https://villaconin.mx/reportes/mysqli.php', personForm)
        .then(response => {
          setTimeout(() => {
            // eslint-disable-next-line no-console
            _.forEach(response.data, (val, key) => {
              // eslint-disable-next-line no-console
              let d = []
              let label = key
              // eslint-disable-next-line no-console
              if (Object.keys(val).length < 12) {
                for (let index = 1; index <= 12; index++) {
                  let x = _.find(val, function(o) { return _.parseInt(o.mes) === index })
                  if (_.isUndefined(x)) {
                    d.push(0)
                  }
                  else {
                    d.push(x.total)
                  }
                }
              } else {
                _.forEach(val, (v) => { // meses
                  d.push(v.total)
                }) 
              }
              this.sets.push({
                label: label,
                backgroundColor: this.backgrounds[key],
                data: d
              })
            })
          }, 1000);
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.error(error)
          this.errored = true
        })
        .finally(() => this.loading = false)
          // Overwriting base render method with actual data.
          setTimeout(() => {
             this.renderChart({
              labels: ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre'],
              datasets: this.sets
          }, {responsive: true, maintainAspectRatio: false})
          }, 3000);
    },
     toFormData: function(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
        }
        return formData;
      }
    } 
}
</script>
