<template>
    <div class="">
      <iframe class="col-lg-12 iframe" height="100%" :src="'https://villaconin.mx/Config/Arbol/CostoPromedio/'" frameborder="0" allowfullscreen></iframe>
      <!-- <div class="row text-center" align="center">
        <div class="col-sm-3">
          <datepicker v-model="fechaInicio" placeholder="Seleccione el Inicio"  ></datepicker>
        </div>
        <div class="col-sm-3 text-center">
          <datepicker v-model="fechaFin" placeholder="Seleccione el Final"  ></datepicker>
        </div>
      </div>
      <v-jstree :data="data" :show-checkbox="false" multiple allow-batch whole-row @item-click="itemClick"></v-jstree>
      <button @click="addItem()">Add</button> -->
    </div>
</template>
<script>
import VJstree from 'vue-jstree'
import Datepicker from 'vuejs-datepicker';
const axios = require('axios');
const moment = require("moment")
var _ = require('lodash');
export default {
  components: {
    VJstree,
    Datepicker
  },
  data () {
    return {
      fechaInicio: '',
      fechaFin: '',
      data: []
    }
  },
  watch:{
    fechaInicio () {
      if (this.fechaFin !== '') {
        this.getAllFunctions()
      }
    },
    async fechaFin () {
      if (this.fechaInicio !== '') {
        this.getAllFunctions()
      }
    }
  },
  async mounted () {
  },
  methods: {
    async getAllFunctions () {
      await this.getData('numeroEventos').then((data) => {
        // eslint-disable-next-line no-console
        console.log('DATATATATA', data)
        this.data.push({
          ref: "cantidadEventos",
          text: 'Cantidad de eventos: ' + data
        })
        setTimeout(() => {
          this.addItem('cantidadEventos')
        }, 2000);
      })
    },
    async getData (opcion) {
      return await new Promise((resolve, reject) => {
        try {
          let payload = {
            fecha1: moment(this.fechaInicio).format('YYYY-MM-DD'),
            fecha2: moment(this.fechaFin).format('YYYY-MM-DD'),
            opcion: opcion
          }
          let personForm = this.toFormData(payload);
          axios
            .post('https://villaconin.mx/reportes/costoPromedio.php', personForm)
            .then(response => {
                if (_.isEmpty(response)) {
                  resolve(0)
                } else {
                  resolve(response.data)
                }
            })
            .catch(error => {
              this.errored = true
              // eslint-disable-next-line no-console
              console.log(error)
            })
            .finally(() => this.loading = false)
        }
        catch (error) {
          reject(error)
        }
      })
    },
    itemClick (node) {
      // eslint-disable-next-line no-console
      console.log(node.model.text + ' clicked !', node)
      node.model.opened = !node.model.opened
    },
    toFormData(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
      }
      return formData;
    }
  }
}
</script>
<style>
.iframe {
  height: 1000px !important;
  width: 100%;
}
</style>